/* eslint-disable */
import React from 'react'
import PropTypes from 'prop-types'
import ReactTour from 'reactour'

import { slugify } from '../../Helpers'

export default function Tour({ steps, isOpen, handleClose }) {
  const stepsArr = steps.map((step) => {
    return {
      ...step,
      content: (
        <>
          <h2
            className="h-s p-v-0"
            id={'page-tour-step-' + slugify(step.title)}
          >
            {step.title}
          </h2>
          <p>{step.body}</p>
        </>
      ),
    }
  })

  return (
    <ReactTour
      onAfterOpen={() => {
        document.body.style.overflowY = 'hidden'
      }}
      onBeforeOpen={() => {
        document.body.style.overflowY = 'auto'
      }}
      onBeforeClose={() => {
        document.body.style.overflowY = 'auto'
      }}
      steps={stepsArr}
      isOpen={isOpen}
      onRequestClose={handleClose}
      showNumber={false}
      lastStepNextButton={
        <button
          className="button button--primary p-v-xxs p-h-xs"
          id="page-tour-start-now"
        >
          Start now
        </button>
      }
      nextButton={
        <button
          className="button button--primary p-v-xxs p-h-xs"
          id="page-tour-next-step"
        >
          Next
        </button>
      }
      prevButton={<></>}
      showCloseButton={false}
      rounded={4}
    />
  )
}

Tour.protoTypes = {
  steps: PropTypes.array,
  isOpen: PropTypes.bool.isRequired,
  handleClose: PropTypes.func.isRequired,
}

Tour.defaultProps = {
  steps: [],
}
